import ProductPage from "@/components/pages/ProductPage";

/**
 * SEO Metadata (SAFE for App Router)
 */
export async function generateMetadata({ params }) {
  const slug = String(params?.slug || "");

  try {
    const res = await fetch(`/api/products/${slug}`, {
      next: { tags: ["products"] },
    });

    if (!res.ok) {
      return {
        title: "Product Not Found | TanzeenFood",
        description: "The product you're looking for doesn't exist.",
      };
    }

    const { product } = await res.json();

    return {
      title: `${product.name} | TanzeenFood - Premium Natural Products`,
      description: product.description,
      openGraph: {
        title: product.name,
        description: product.description,
        images: product.images?.length ? [product.images[0]] : [],
      },
    };
  } catch (error) {
    return {
      title: "Error | TanzeenFood",
      description: "Something went wrong while loading the product.",
    };
  }
}

/**
 * Page Component
 */
export default async function Page({ params }) {
  const slug = String(params?.slug || "");
  const categorySlug = String(params?.category || "");

  const [productRes, categoryRes, relatedRes] = await Promise.all([
    fetch(`/api/products/${slug}`, { next: { tags: ["products"] } }),
    fetch(`/api/categories?slug=${categorySlug}`, {
      next: { tags: ["categories"] },
    }),
    fetch(`/api/products?category=${categorySlug}&limit=8`, {
      next: { tags: ["products"] },
    }),
  ]);

  const productData = productRes.ok ? await productRes.json() : null;
  const categoryData = categoryRes.ok
    ? await categoryRes.json()
    : { categories: [] };
  const relatedData = relatedRes.ok
    ? await relatedRes.json()
    : { products: [] };

  const product = productData?.product || null;
  const currentCategory = categoryData.categories?.[0] || null;

  const relatedProducts = (relatedData.products || [])
    .filter((item) => item.id !== product?.id)
    .slice(0, 4);

  return (
    <ProductPage
      product={product}
      currentCategory={currentCategory}
      relatedProducts={relatedProducts}
    />
  );
}
